$bodyColor: "red";
$textColor: #000;
$warningColor: rgba(220, 53, 69, 0.4);
$okColor: rgba(40, 167, 69, 0.4);
body{
	color: $bodyColor;
}
/*---pdf page--*/
.btn{
	border-radius: 0;
}
.custom-file-label{
	border-radius: 0;
}
.list-group-item{
	padding: 5px;
	border-radius: 0;
}
hr{
	margin-top: 5px;
	margin-bottom: 5px;
}	
.form-control{
	border-radius: 0;
}
.alert{
	padding: 5px;
	border-radius: 0;
}
.imageTexts .btn{
	min-width: 25px;
    border-radius: 50%;
    white-space: nowrap;
    padding: 2px;
    font-size: 13px;
    border: 1px solid rgba(0,0,0,0.3);
}
.imageTexts .simpBtn{
	border-radius: 0;
}
.imageParent{
	position: relative;
	z-index: 1;
	border: 3px solid #ccc;
    box-shadow: 0 0 15px rgba(0,0,0,0.2);	
}
.imageTexts{
	position: absolute;
	z-index: 2;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
    font-size: 15px;
    font-weight: bold;
    color: $textColor;	
}
.imageText{
	border: 1px solid transparent;
}
.imageText:hover{
	border: 1px dashed #ccc;
}
.imageText.warningText{	
	background: $warningColor;
}
.imageText.okText{
	background: $okColor;	
}
.imageTextFileldParent{
	z-index: 20;
	position: absolute;		
}
.imageTextFileld{
    padding: 0;
    border-radius: 0;
	background: transparent;	
    font-size: 15px;
    font-weight: bold;
    color: $textColor;	
	resize: both;
	width: 200px;
}
.imageTextFileld.form-control-warning{
	background: $warningColor;
}
.imageTextFileld.form-control-ok{
	background: $okColor;
}
.imageTextFileldButtons{
	position: absolute;
	z-index: 2;
    left: 100%;
    top: 0;
    white-space: nowrap;
    background: #cce5ff;
    border-radius: 20px;
}
.imageTextFileldMessageParent{
    position: absolute;
    left: 0;
    width: 220px;
}
.imageTextFileldMessageBtn{
	text-align: right;
	padding-top: 5px;
	display: block;
}
.imageTextFileldMessageBtn .btn{
	padding-left: 10px;
	padding-right: 10px;
}
.imagePreviewsContainer{
	position: fixed;
	right: 0;
	top: 0;
	bottom: 0;
	background:#fff;
	z-index: 10;
}
.imagePreview{
	height: 60px;
	width: 60px;
	overflow: hidden;
	position: relative;
	margin: 5px;
	border: 2px solid #ccc;
	cursor: pointer;
}
.imagePreview img{
	width: 100%;
	height: auto;
}
.imagePreviewNumber{
	position: absolute;
	z-index: 2;
	left: 0;
	right: 0;
	top: 50%;
	margin-top: -7px;
	width: 100%;
	text-align: center;
	color: #000;
	font-size: 15px;
	line-height: 15px;
	height: 15px;	
	display: block;	
}
.imagePreviewNumber span{
	border-radius: 20px;
	border: 2px solid #ccc;	
	display: inline-block;
	vertical-align: middle;	
	background: #fff;
    font-weight: bold;
    padding: 1px 4px;
}
.imagePreview.active,
.imagePreview:hover{
	border-color: #0056b3;
}
.imagePreview.active .imagePreviewNumber span,
.imagePreview:hover .imagePreviewNumber span{
	border-color: #0056b3;
}
.imagePreviewsPrev,
.imagePreviewsNext{
    text-align: center;
    font-size: 30px;
    line-height: 1;
    cursor: pointer;
    color: #ccc;
    margin-bottom: -5px;
}
.imagePreviewsNext{
	margin-bottom: 0;
	margin-top: -5px;
}